@import 'src/common/styles/sass/varialbles';

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.App {
  font-family: $firstFont;
  color: #9f9f9f;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.95;

  address {
    font-style: normal;
  }

  p {
    color: $textSecondaryColor;
    margin-top: 0;
  }

  h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
  }
}
